.workItem{border:1px solid var(--medGray);border-radius:4px;box-shadow:2px 2px 6px 0 var(--medGray);display:block;height:500px;position:relative;cursor:pointer;overflow:hidden;z-index:0}.workItem:link,.workItem:visited{text-decoration:none}.workItem__image{background-size:cover;background-position:center top;box-shadow:0px 0px 10px 0px var(--medGray);width:100%;height:calc(300px / (16 / 9))}.workItem__label{font-size:1.15rem;line-height:1.2em;text-align:center;padding:15px 15px 7px}.workItem__label:after{content:'';background-color:var(--darkGray);display:block;width:5ex;height:1px;margin:10px auto 0}.workItem__title{text-align:center;width:100%;padding:0 15px}.workItem__title__text{font-size:2rem;line-height:1.1em;min-height:4ch}.workItem__description{padding:15px 15px 0}.workItem__description__text{line-height:normal}.workItem .tags{padding:10px;width:100%;position:absolute;left:0;bottom:0}.workItem .tag:hover{color:var(--darkGray)}.workItem .tag:hover .tag__hole{background-color:var(--lightGray)}.workItem .tag:hover.tag--web-development{background-color:var(--yellowColor)}.workItem .tag:hover.tag--web-development .tag__point{background-color:var(--yellowColor)}.workItem .tag:hover.tag--design{background-color:var(--purpleColor)}.workItem .tag:hover.tag--design .tag__point{background-color:var(--purpleColor)}.workItem .tag:hover.tag--podcast-production{background-color:var(--greenColor)}.workItem .tag:hover.tag--podcast-production .tag__point{background-color:var(--greenColor)}.workItem .tag:hover.tag--video-production{background-color:var(--blueColor)}.workItem .tag:hover.tag--video-production .tag__point{background-color:var(--blueColor)}.workItem .tag:hover.tag--animation{background-color:var(--orangeColor)}.workItem .tag:hover.tag--animation .tag__point{background-color:var(--orangeColor)}.mobileView .workItem{flex:0 1 100%;margin:0 0 20px;display:flex;height:auto}.mobileView .workItem__image{flex:0 0 auto;background-position:center top;width:220px;height:auto}.mobileView .workItem__details{flex:0 1 auto}.mobileView .workItem__title__text{min-height:auto}.mobileView .workItem .tags{position:static}@media screen and (max-width: 480px){.mobileView .workItem{flex-wrap:wrap}.mobileView .workItem__image{flex:1 0 100%;width:auto;height:200px}}
