.filters__list{display:flex;flex-wrap:wrap;justify-content:center;max-width:1024px;margin:20px auto 10px}.filter{font-size:1.25rem;line-height:1;background-color:transparent;border:1px solid var(--darkGray);padding:10px;margin:10px;cursor:pointer;position:relative;transition:background-color 0.35s ease-in-out;overflow:hidden}.filter::selection{background:transparent}.filter:hover:after{transform:scaleX(1);transform-origin:center left}.filter:after{content:"";width:100%;height:100%;position:absolute;left:0;top:0;transform:scaleX(0);transform-origin:center right;transition:transform 0.35s ease-in-out;z-index:-1}.filter--red:after{background-color:var(--redColor)}.filter--yellow:after{background-color:var(--yellowColor)}.filter--purple:after{background-color:var(--purpleColor)}.filter--green:after{background-color:var(--greenColor)}.filter--blue:after{background-color:var(--blueColor)}.filter--orange:after{background-color:var(--orangeColor)}.filter--active{color:var(--lightGray);background-color:var(--darkGray)}
