html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}.contentWrapper{padding:85px 20px 0}.homePage .contentWrapper{padding-top:0}.homePage .work__wrapper,.homePage .aboutMe__wrapper{margin-bottom:100px}.tags{display:flex;flex-wrap:wrap;justify-content:flex-end}.tag{background-color:var(--lightGray);border:1px solid var(--darkGray);border-left:none;padding:5px;margin:5px 5px 5px 23px;position:relative;transition:all 0.2s ease-in-out}.tag__point{width:20px;height:20px;position:absolute;left:-10px;top:3px;transform:rotate(45deg);border:1px solid var(--darkGray);background-color:var(--lightGray);transition:all 0.2s ease-in-out;z-index:-1}.tag__hole{position:absolute;left:-4px;top:50%;transform:translateY(-50%);border:1px solid var(--darkGray);border-radius:5px;width:5px;height:5px}:root{--lightGray: #ffffff;--medGray: #cccccc;--darkGray: #191919;--redColor: #f6a6b1;--yellowColor: #fffdac;--purpleColor: #a785bf;--greenColor: #aae198;--blueColor: #8798bf;--orangeColor: #ffd1ac}*{box-sizing:border-box}body{font-family:'fira-sans', sans-serif;font-size:16px;color:var(--darkGray);background-color:var(--lightGray)}a:link,a:visited{color:var(--darkGray);text-decoration:underline}a:hover{color:var(--darkGray)}
