.workDetails__header{text-align:center;margin-bottom:1rem}.workDetails__header__label{font-size:1.15rem;line-height:normal}.workDetails__header__label:after{content:'';background-color:var(--darkGray);display:block;width:5ex;height:1px;margin:5px auto 0}.workDetails__header__title{font-size:2.25rem;line-height:normal}.workDetails__info{padding:105px 20px 20px;width:auto;max-width:480px}.workDetails__info__description__text{font-size:1.2rem;line-height:1.45}.workDetails__info__footer{padding:0;margin-top:30px}.workDetails__info__footer__links{text-align:right;margin-top:15px}.workDetails__info__footer__links a:link,.workDetails__info__footer__links a:hover,.workDetails__info__footer__links a:visited{font-size:1.2rem;line-height:1.45;text-decoration:underline}.workDetails__info__footer__linkToOtherWork{display:flex;justify-content:space-between;align-items:flex-end;margin-top:30px}.workDetails__info__footer__linkToOtherWork__link{display:block}.workDetails__info__footer__linkToOtherWork__icon{font-size:2rem;display:block}.workDetails__info__footer__linkToOtherWork__icon--next{margin-left:auto}.mobileView .workDetails__info{grid-row:1 / 2;padding:105px 0 20px;max-width:none}
