.contact__wrapper{width:100%;max-width:620px;margin:0 auto 20px}.contact__form__input{font-family:'fira-sans', sans-serif;font-size:1.2rem;border:0;border-bottom:1px solid var(--darkGray);padding:10px;display:block;width:100%}.contact__form__input+*{margin-top:20px}.contact__form__input--message{border:1px solid var(--darkGray);margin-top:50px;resize:none}.contact__form__input--submit{color:var(--darkGray);border:1px solid var(--darkGray);background-color:var(--lightGray);margin:70px auto 0;width:100%;max-width:300px;transition:background-color 0.2s ease-in-out;cursor:pointer}.contact__form__input--submit:focus,.contact__form__input--submit:hover{background-color:var(--medGray)}.contact__form__input:focus{outline:none}
